import './CarouselProductos.css'
import 'swiper/bundle'
import SwiperCore, { Autoplay, Navigation, Pagination, Scrollbar } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/react'
import { observer } from 'mobx-react-lite'
import { useHistory } from 'react-router'

import React, { useEffect, useState } from 'react'
import { IonAlert } from '@ionic/react'

import { RUTAS } from '../constants'
import { useStores } from '../models/root-store/root-store-context'
import { iProducto } from '../models/favorito-store/favorito-store'
import ProductoCard, { iCardState } from '../components/ProductoCard'

SwiperCore.use([Navigation, Scrollbar, Pagination, Autoplay])

interface CarouselProductosProps {
  productos: iProducto[]
  titulo: string
  Movil: boolean
}

export const CarouselProductos: React.FC<CarouselProductosProps> = observer((props) => {
  const rootStore = useStores()
  const history = useHistory()
  const Movil = /Mobi/.test(navigator.userAgent)
  const [AlertBloqueoTitle, setAlertBloqueoTitle] = useState<string>('')
  const [AlertBloqueoMessage, setAlertBloqueoMessage] = useState<string>('')
  const [AlertBloqueoAction, setAlertBloqueoAction] = useState<any>(null)
  const [AlertBloqueo, setAlertBloqueo] = useState<boolean>(false)
  const [cardsState, setCardsState] = useState([] as iCardState[])

  useEffect(() => {
    const cards: iCardState[] = props.productos.map((prod, index) => {
      return { producto: prod, cantidadCarrito: 1, index: index }
    })
    setCardsState(cards)
  }, [])

  return (
    <>
      <IonAlert
        isOpen={AlertBloqueo}
        onDidDismiss={() => setAlertBloqueo(false)}
        header={'Acceso restringido'}
        message={AlertBloqueoMessage}
        buttons={
          AlertBloqueoAction
            ? [
                {
                  text: 'Cancelar',
                  role: 'cancel',
                  cssClass: 'secondary',
                },
                {
                  text: AlertBloqueoTitle,
                  handler: AlertBloqueoAction,
                },
              ]
            : [
                {
                  text: 'Cancelar',
                  role: 'cancel',
                  cssClass: 'secondary',
                },
              ]
        }
      />
      <div className={'carousel-productos-container-separador'} />
      <div className="carousel-productos-container">
        <div className="d-flex justify-content-center flex-column my-3 my-lg-0">
          <span className="titulo-carousel">{props.titulo}</span>
        </div>
        <Swiper
          updateOnWindowResize={false}
          observer
          initialSlide={0}
          navigation={Movil ? false : true}
          pagination={false}
          scrollbar={{ hide: false }}
          slidesPerView={'auto'}
          autoplay={{
            delay: 3500,
            disableOnInteraction: false,
          }}
          speed={400}
          className={'carousel-productos'}
        >
          {props.productos.map((producto, idx) => (
            <SwiperSlide key={idx}>
              <ProductoCard containerClass={''} producto={producto} />
            </SwiperSlide>
          ))}
        </Swiper>
        <div className="d-flex justify-content-center cpointer py-2" onClick={() => history.push(RUTAS.BUSCAR)}>
          <span className="footer-carousel-productos">+ Ver más</span>
        </div>
      </div>
    </>
  )
})
