import React, { useState } from 'react'
import { observer } from 'mobx-react-lite'
import * as FIcon from 'react-feather'
import './Header.css'
import '../../pages/Fabricante/Home.css'
import HomeSidebar from './HomeSidebar'
import { useStores } from '../../models/root-store/root-store-context'
import { useOpenPage } from '../../helpers-ts'
import HomeSidebarTransportista from '../Transportista/HomeSidebarTransportista'
import { RUTAS, RUTAS_APP, RUTAS_APP_FABRICANTE } from '../../constants'
import { useHistory } from 'react-router'
import { ICONOS } from '../../helpers-ts/iconos'

type HeaderRegistroProps = {
  botonIzquierdo?: any
  botonDerecho?: any
  accionBotonDerecho?: any
  accionBotonIzquierdo?: any
  color?: string
  titulo?: string | null
  sub_titulo?: string | null
  mostrarBarra?: boolean
  subBarraInterna?: any
  subBarraExterna?: any
}

export const Header: React.FC<HeaderRegistroProps> = observer((props) => {
  const rootStore = useStores()
  const { loginStore, transportistaStore } = rootStore
  const openPage = useOpenPage()
  const botonIzquierdo = props.botonIzquierdo || <FIcon.Menu size={30} color={'#ffffff'} />
  const accionBotonIzquierdo = props.accionBotonIzquierdo || (() => setMostrarMenu(!MostrarMenu))
  const botonDerecho = props.botonDerecho || <div style={{ width: 46, opacity: 0 }} />
  const color = props.color || '#303AB2'
  const titulo = props.titulo || ''
  const sub_titulo = props.sub_titulo || null
  const mostrarBarra = props.mostrarBarra || false
  const [MostrarMenu, setMostrarMenu] = useState<boolean>(false)
  const history = useHistory()

  let aprobado
  if (loginStore.modo_uso_app === 30) {
    aprobado = loginStore.transportista ? loginStore.transportista.aprobado : false
  } else if (loginStore.modo_uso_app === 20) {
    aprobado = loginStore.fabricante ? loginStore.fabricante.aprobado : false
  } else {
    aprobado = true
  }

  const modal_menu_visible = (visible: boolean) => {
    setMostrarMenu(visible)
  }

  return (
    <div
      style={{ backgroundColor: color }}
    >
      {/*Se usa un menú diferente en base a que tipo de rol se está usando en la  app*/}
      {loginStore.modo_uso_app === 20 && (
        <HomeSidebar fabricante={loginStore.fabricante} visible={MostrarMenu} set_visible={modal_menu_visible} />
      )}
      {loginStore.modo_uso_app === 30 && (
        <HomeSidebarTransportista visible={MostrarMenu} set_visible={modal_menu_visible} />
      )}
      <div className={'row pt-4 mx-0 px-0'}>
        <div className={'col-2 text-right px-1'} onClick={() => accionBotonIzquierdo()}>
          {botonIzquierdo}
        </div>
        <div className={'col-8 text-center d-flex justify-content-center align-items-center'}>
          <span
            className={'logo-abi-fabricante'}
            onClick={() => {
              if (loginStore.modo_uso_app === 30) {
                openPage(RUTAS_APP.HOME)
              } else if (loginStore.modo_uso_app === 20) {
                openPage(RUTAS_APP_FABRICANTE.HOME)
              } else {
                openPage(RUTAS.ROOT)
              }
            }}
          >
            {ICONOS.abi}
          </span>
        </div>
        <div className={'col-2 text-left px-1 image-container-header-home'} onClick={() => props.accionBotonDerecho()}>
          {botonDerecho}
        </div>
      </div>
      <div className={'text-center'}>
        {titulo && (
          <div className={'text-white mt-3'}>
            <strong>{titulo}</strong>
          </div>
        )}
        {!!sub_titulo && (
          <div
            className={'text-white mb-3 d-flex justify-content-center align-items-start'}
            style={{ lineHeight: '0.8rem' }}
          >
            <small>{sub_titulo}</small>
          </div>
        )}
        {props.subBarraInterna && <>{props.subBarraInterna}</>}
      </div>
      {loginStore.modo_uso_app === 20 && aprobado && mostrarBarra && (
        <div className={'menu-bar-aprobado pb-2'}>
          <img onClick={openPage(RUTAS_APP_FABRICANTE.BALANCE)} alt={''} src={'assets/icon/estadisticas-white.png'} />
          <img onClick={openPage(RUTAS_APP_FABRICANTE.CATALOGO)} alt={''} src={'assets/icon/stock-white.png'} />
          <span onClick={openPage(RUTAS_APP_FABRICANTE.VENTAS)}>{ICONOS.movimientos}</span>
          <span onClick={openPage(RUTAS_APP_FABRICANTE.BALANCE)}>{ICONOS.facturacion}</span>
        </div>
      )}
      {!!props.subBarraExterna && <>{props.subBarraExterna}</>}
      {/* Boton menu para el chat */}
      {/* <div className={'boton-menu'} onClick={() => setMostrarMenu(true)}>
        <FIcon.Menu size={30} color={'#ffffff'} />
      </div> */}
    </div>
  )
})

export default Header
