import './ProductoDetail.css'
import './Home.css'

import 'swiper/bundle'
import { FirebaseAnalytics } from '@capacitor-community/firebase-analytics'
import { Capacitor } from '@capacitor/core'
import { Rating } from '@material-ui/lab'
import { Share } from '@capacitor/share'
import { IonAlert, IonButtons, IonContent, IonHeader, IonModal, IonSpinner, IonTitle, IonToolbar } from '@ionic/react'
import SwiperCore, { Autoplay, Navigation, Pagination, Scrollbar } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/react'
import { MdAddCircleOutline, MdRemoveCircleOutline } from 'react-icons/md'
import { AiOutlineHeart, AiOutlineShareAlt } from 'react-icons/ai'
import { observer } from 'mobx-react-lite'
import numeral from 'numeral'
import React, { useEffect, useRef, useState } from 'react'
import * as FIcon from 'react-feather'
import ReactImageMagnify from 'react-image-magnify'
import { HeaderRegistro, MetaTag, Page } from '../../components'
import { formatCantidadCarrito } from '../../helpers'
import { DetailViewProps, useToaster, useVerificarLoginEdad } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { SubHeader } from '../../components/SubHeader'
import { CarouselProductos } from '../../components/CarouselProductos'
import { Producto as ProductoModel } from '../../models/types/Producto'
import { SnapshotIn } from 'mobx-state-tree'
import { RUTAS } from '../../constants'
import { TransformWrapper, TransformComponent } from 'react-zoom-pan-pinch'
import { ICONOS } from '../../helpers-ts/iconos'

const moment = require('moment-timezone')
SwiperCore.use([Navigation, Scrollbar, Pagination, Autoplay])

const productoDefault = {
  id: '',
  fabricante: { nombre: '', rating: 0 },
  nombre: '',
  descripcion: '',
  descripcion_extensa: '',
  etiquetas: '',
  cantidad_mayorista: 0,
  precio: 0,
  precio_mayorista: 0,
  cantidad_disponible: 0,
  fecha_agregado: '',
  fecha_actualizado: '',
  compra_mejorada: false,
  imagenes: [{ imagen: '', resoluciones: { thumbnail: '', medium: '', fullres: '' } }],
  productos_relacionados: [],
  comentarios: [],
}

interface iProducto extends SnapshotIn<typeof ProductoModel> {}

const  base_url  = 'https://abi.com.py'

export const ProductoDetail: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { loginStore, carritoStore, favoritoStore } = rootStore
  const [Movil, setMovil] = useState(false)
  const [ProductoSeleccionado, setProductoSeleccionado] = useState(null as any)
  const [CantidadCarrito, setCantidadCarrito] = useState(1)
  const [ImagenProductoSeleccionado, setImagenProductoSeleccionado] = useState(productoDefault.imagenes[0].imagen)
  const [ResolucionesProductoSeleccionado, setResolucionesProductoSeleccionado] = useState(
    productoDefault.imagenes[0].resoluciones,
  )
  const [Loading, setLoading] = useState(false)
  const [ShowDescription, setShowDescription] = useState(true)
  const [ShowEspecificaciones, setShowEspecificaciones] = useState(false)
  const [ShowComentarios, setShowComentarios] = useState(false)
  const [ProductoNoEncontrado, setProductoNoEncontrado] = useState(false)
  const [ModalComentario, setModalComentario] = useState(false)
  const [Comentario, setComentario] = useState('')
  const addToast = useToaster()
  const [modalProductoMovil, setModalProductoMovil] = useState(false)
  const [ModalImagen, setModalImagen] = useState(false)
  const [AlertBloqueo, setAlertBloqueo] = useState<boolean>(false)
  const [AlertBloqueoTitle, setAlertBloqueoTitle] = useState<string>('')
  const [AlertBloqueoMessage, setAlertBloqueoMessage] = useState<string>('')
  const [AlertBloqueoAction, setAlertBloqueoAction] = useState<any>(null)
  const verificarEdadEstado = useVerificarLoginEdad()
  const Content = useRef<HTMLIonContentElement | null>(null)
  const urlPagina = base_url + window.location.pathname
  const scrollRef = useRef<any>()
  const [metaTags, setMetaTags] = useState<MetaTag[]|undefined>()

  useEffect(() => {
    setLoading(true)
    obtenerProducto()
    setMovil(window.innerWidth <= 500)

    if (scrollRef) {
      scrollRef.current?.scrollTop()
    }
  }, [match])

  const obtenerProducto = () => {
    Content.current?.scrollToTop(100)
    setLoading(true)
    const idProducto = match.params.id
    // @ts-ignore
    rootStore.environment.api.productoDetalle(idProducto).then((result) => {
      if (result.kind === 'ok') {
        if (Capacitor.getPlatform() === 'web') {
          const dataLayer = (window as any).dataLayer || []

          // @ts-ignore
          function gtag() {
            dataLayer.push(arguments)
          }

          // @ts-ignore
          gtag('event', 'view_item', {
            usuario_id: loginStore.isLoggedIn ? loginStore.id : null,
            producto_id: result.data.id,
            producto_nombre: result.data.nombre,
          })
        } else {
          FirebaseAnalytics.logEvent({
            name: 'page_view',
            params: {
              page: 'producto',
              usuario_id: loginStore.isLoggedIn ? loginStore.id : null,
              producto_id: result.data.id,
              producto_nombre: result.data.nombre,
            },
          })
            .then((res: any) => {
              console.log(res)
            })
            .catch((error: any) => {
              console.error(error)
            })
        }
        let imagenProducto = ''
        const producto = result.data
        // @ts-ignore
        setProductoSeleccionado(producto)

        if (result.data.imagenes && result.data.imagenes.length > 0) {
          imagenProducto = result.data.imagenes[0].imagen
          setResolucionesProductoSeleccionado((result.data.imagenes[0] as any).resoluciones)
        } else {
          imagenProducto = 'https://abi.com.py/assets/abi-logo-producto.png'
        }
        setImagenProductoSeleccionado(imagenProducto)

        setMetaTags([
          {name: 'og:image', content: imagenProducto},
          {name: 'og:title', content: producto.nombre},
          {name: 'title', content: producto.nombre},
          {name: 'og:description', content: producto.descripcion_extensa},
          {name: 'description', content: producto.descripcion_extensa},
          {name: 'og:locale', content: "es_PY"},
          {name: 'og:type', content: "website"},
        ])

        setTimeout(() => setLoading(false), 1000)
      } else if (result.kind === 'not-found') {
        setProductoNoEncontrado(true)
      }
    })
  }

  const validarCantidadAgregarCarrito = (producto: iProducto, cantidad: any) => {
    let obj = carritoStore.productos.find((o) => o.id === producto.id)
    if (obj) {
      if (obj.cantidad + cantidad > producto.cantidad_disponible) {
        addToast(`No hay suficiente cantidad en stock. Cantidad disponible: ${producto.cantidad_disponible}.`)
        return false
      }
    } else {
      if (cantidad > producto.cantidad_disponible) {
        addToast(`No hay suficiente cantidad en stock. Cantidad disponible: ${producto.cantidad_disponible}.`)
        return false
      }
    }
    return true
  }

  const addProductoCarrito = (producto: any) => {
    if (producto.no_apto_menores) {
      const estado = verificarEdadEstado()
      if (estado.case !== 10) {
        setAlertBloqueoTitle(estado.title)
        setAlertBloqueoMessage(estado.message)
        setAlertBloqueoAction(estado.action)
        setAlertBloqueo(true)
        return false
      }
    }
    let obj = carritoStore.productos.find((o) => o.id === producto.id)
    let productoCarrito
    if (obj) {
      for (let i = 0; i < CantidadCarrito; i++) {
        carritoStore.sumarProducto(producto)
      }
    } else {
      productoCarrito = {
        ...producto,
        cantidad: CantidadCarrito,
        fabricante: producto.fabricante.nombre,
        imagenes: producto.imagenes.length > 0 ? producto.imagenes[0].imagen : '',
        categoria: producto.categoria.nombre,
      }
      carritoStore.addProducto(productoCarrito)
    }
    if (loginStore.isLoggedIn) {
      rootStore.environment.api.agregarEliminarProductoCarrito(producto.id, null).then((result) => {
        if (result.kind === 'ok') {
        }
      })
    }
    setCantidadCarrito(1)
  }

  const seleccionarImagen = (imagen: any, resoluciones: any) => {
    setImagenProductoSeleccionado(imagen)
    setResolucionesProductoSeleccionado(resoluciones)
  }

  const toggleFavorito = (producto: iProducto) => {
    if (!loginStore.isLoggedIn) {
      addToast('Inicie sesión para agregar a sus favoritos.')
      return
    }
    const fav = favoritoStore.favorito(producto)
    if (!!fav) {
      const estado = fav.toggleActivo()
      if (fav.id) {
        rootStore.environment.api.updateFavorito(fav.id, estado).then((result) => {
          if (result.kind === 'ok') {
            console.log(result)
          }
        })
      }
    } else {
      favoritoStore.addFavorito(producto)
      rootStore.environment.api.addFavorito(producto.id).then((result) => {
        if (result.kind === 'ok') {
          rootStore.environment.api.getFavoritos().then((result) => {
            if (result.kind === 'ok') {
              favoritoStore.setFavoritosAPI(result.data)
            }
          })
        }
      })
    }
  }

  const compartirProducto = () => {
    if (!Movil) {
      const el = document.createElement('input')
      el.value = urlPagina
      el.id = 'url-input'
      document.body.appendChild(el)
      el.select()
      document.execCommand('copy')
      el.remove()
      addToast('Copiado al portapapeles', 'success')
    } else {
      Share.share({
        url: urlPagina,
        dialogTitle: 'Produto',
      })
        .then((result) => console.log(result))
        .catch((error) => console.log(error))
    }
  }

  const guardarComentario = async () => {
    if (Comentario.trim().length < 20) {
      addToast('El comentario debe tener por lo menos 20 caracteres', 'warning')
      return
    }
    const result = await rootStore.environment.api.simple_post('/comentario-producto/', {
      comentario: Comentario,
      producto: ProductoSeleccionado?.id,
    })
    if (result.kind === 'ok') {
      addToast('Comentario agregado exitosamente', 'success')
      setComentario('')
      setModalComentario(false)
      obtenerProducto()
    } else {
      addToast('Ocurrió un error al agregar el comentario', 'error')
    }
  }

  const notificarDisponibilidad = async () => {
    if (!loginStore.isLoggedIn) {
      addToast('Inicie sesión para que le notifiquemos la disponibilidad')
      return
    }
    const result = await rootStore.environment.api.simple_post(`/notificar-disponibilidad/${ProductoSeleccionado?.id}/`)
    if (result.kind === 'ok') {
      if (result.response.eliminado) {
        addToast('Notificación desactivada!')
      } else {
        addToast('Te notificaremos cuando el producto esté disponible')
      }
      obtenerProducto()
    } else if (result.kind === 'bad-data') {
      addToast('El producto no está disponible o ya está disponible en stock')
    } else {
      addToast('Ocurrió un error al comunicarse con el servidor', 'error')
    }
  }

  const anchoImagenesThumb = Movil ? 50 : 80

  const ImagenesProducto = () => {
    return (
      <>
        <IonModal
          isOpen={ModalImagen}
          onDidDismiss={() => setModalImagen(false)}
          swipeToClose={true}
          backdropDismiss={true}
        >
          <div id={'modal-imagen'}>
            <div className={'btn-cerrar'} onClick={() => setModalImagen(false)}>
              <FIcon.X color={'#000'} />
            </div>
            <TransformWrapper centerOnInit={true}>
              <TransformComponent>
                <img src={ResolucionesProductoSeleccionado.fullres} style={{ width: '100%', height: '100%' }} />
              </TransformComponent>
            </TransformWrapper>
          </div>
        </IonModal>
        {!Loading && (
          <div
            className=""
            style={{
              flex: 2,
              display: 'flex',
              backgroundColor: 'white',
              justifyContent: Movil ? 'space-between' : 'space-around',
              height: 'fit-content',
            }}
          >
            <div className="swiper-container-imagenes-producto">
              <div id={'imgs-producto-prev'} className={'arrow-container'}>
                <FIcon.ChevronUp size={30} color={'var(--gris-oscuro-abi)'} style={{ marginTop: -5 }} />
              </div>
              <Swiper
                slidesPerView={3}
                direction={'vertical'}
                className={'producto-detail-imagenes-swiper'}
                navigation={{
                  prevEl: '#imgs-producto-prev',
                  nextEl: '#imgs-producto-next',
                }}
              >
                {ProductoSeleccionado?.imagenes.map((imagen: any, idx: number) => {
                  return (
                    <SwiperSlide key={idx}>
                      <div style={{ width: '100%', display: 'flex', justifyContent: 'center' }}>
                        <div
                          className="imagen-thumbnail-detalle-producto"
                          key={idx}
                          style={{ height: 'fit-content' }}
                          onClick={() => seleccionarImagen(imagen.imagen, imagen.resoluciones)}
                        >
                          <div
                            style={{
                              cursor: 'pointer',
                              background: '#FFF',
                              display: 'flex',
                              alignItems: 'center',
                            }}
                          >
                            <img
                              className={''}
                              src={imagen.imagen}
                              alt={'item'}
                              style={{ height: anchoImagenesThumb, width: anchoImagenesThumb, objectFit: 'contain' }}
                            />
                          </div>
                        </div>
                      </div>
                    </SwiperSlide>
                  )
                })}
              </Swiper>
              <div id={'imgs-producto-next'} className={'arrow-container'}>
                <FIcon.ChevronDown size={30} color={'var(--gris-oscuro-abi)'} />
              </div>
            </div>

            <div style={{ flex: 1 }}>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'center',
                  alignItems: 'center',
                  background: '#FFF',
                }}
              >
                <div className={'imagen-fullres-detalle-producto'}>
                  {!ProductoSeleccionado.disponible &&
                      <div id="contenedor-etiqueta">
                        <div>Agotado</div>
                      </div>
                  }
                  {!Movil && (
                    <div className={'magnify'}>
                      <ReactImageMagnify
                        {...{
                          smallImage: {
                            alt: ProductoSeleccionado?.descripcion,
                            isFluidWidth: true,
                            src: ResolucionesProductoSeleccionado.medium,
                          },
                          largeImage: {
                            src: ResolucionesProductoSeleccionado.fullres,
                            width: 2000,
                            height: 2000,
                          },
                          enlargedImageContainerStyle: {
                            zIndex: 9999,
                            backgroundColor: '#FFF',
                          },
                          enlargedImageContainerDimensions: {
                            width: '130%',
                            height: '130%',
                          },
                        }}
                      />
                    </div>
                  )}
                  {Movil && (
                    <img
                      onClick={() => setModalImagen(true)}
                      src={ResolucionesProductoSeleccionado.medium}
                      style={{ width: '100%', height: '100%' }}
                    />
                  )}
                </div>
                {Movil && ProductoSeleccionado && (
                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      alignItems: 'center',
                      width: '100%',
                      marginTop: 10,
                    }}
                  >
                    <div style={{ flex: 1, display: 'flex', alignItems: 'center', gap: 10 }}>
                      <Rating name="read-only" size={'small'} value={ProductoSeleccionado.fabricante.rating} readOnly />

                      <div style={{ fontSize: 15, fontWeight: 'bolder' }}>{ProductoSeleccionado.fabricante.rating}</div>
                    </div>
                    <span
                      className="precio-producto py-lg-0 text-left seleccionado "
                      style={{ fontSize: 15, fontWeight: 'bold' }}
                    >
                      Gs. {numeral(ProductoSeleccionado.precio).format('0,0')}
                    </span>
                  </div>
                )}
              </div>
            </div>
          </div>
        )}
      </>
    )
  }

  if (!ProductoSeleccionado || !ProductoSeleccionado.id) {
    return <div></div>
  }

  return (
    <div>
      <Page
        login_required={false}
        solo_cliente={true}
        titulo={ProductoNoEncontrado ? 'Producto no encontrado' : ProductoSeleccionado?.nombre}
        ref={scrollRef}
        id="detalle-producto"
        metaTags={metaTags}
      >
        <IonAlert
          isOpen={AlertBloqueo}
          onDidDismiss={() => setAlertBloqueo(false)}
          header={'Acceso restringido'}
          message={AlertBloqueoMessage}
          buttons={
            AlertBloqueoAction
              ? [
                  {
                    text: 'Cancelar',
                    role: 'cancel',
                    cssClass: 'secondary',
                  },
                  {
                    text: AlertBloqueoTitle,
                    handler: AlertBloqueoAction,
                  },
                ]
              : [
                  {
                    text: 'Cancelar',
                    role: 'cancel',
                    cssClass: 'secondary',
                  },
                ]
          }
        />
        {Movil && (
          <IonModal
            isOpen={modalProductoMovil}
            id={'modal-carrito'}
            onDidDismiss={() => setModalProductoMovil(false)}
            swipeToClose={true}
            backdropDismiss={true}
            cssClass={'modal-transparent-full'}
          >
            <IonHeader>
              <IonToolbar>
                <IonTitle slot="start">
                  {ProductoNoEncontrado ? 'Producto no encontrado' : ProductoSeleccionado?.nombre}
                </IonTitle>
                <IonButtons slot={'end'}>
                  <FIcon.X color={'#000'} className={'mr-2'} onClick={() => setModalProductoMovil(false)} />
                </IonButtons>
              </IonToolbar>
            </IonHeader>
            <IonContent>
              <div className={'h-100 d-flex justify-content-center align-items-center'}>
                <img src={ImagenProductoSeleccionado} alt={'item'} />
              </div>
            </IonContent>
          </IonModal>
        )}
        {ModalComentario && (
          <IonModal
            isOpen={ModalComentario}
            id={'modal-comentario'}
            onDidDismiss={() => setModalComentario(false)}
            swipeToClose={true}
            backdropDismiss={true}
            cssClass={'modal-transparent-full'}
          >
            <HeaderRegistro
              color={'var(--naranja-abi)'}
              botonIzquierdo={
                <span onClick={() => setModalComentario(false)}>
                  <FIcon.ChevronLeft size={30} color={'#ffffff'} />
                </span>
              }
              titulo={'Agregar comentario'}
            />
            <div style={{ padding: 20, background: '#FFF', flexGrow: 1 }}>
              <textarea
                className="form-control"
                rows={5}
                placeholder={'Escriba su comentario aquí'}
                value={Comentario}
                onChange={(e) => setComentario(e.target.value)}
              />
            </div>

            <div className={'text-right pb-2 pr-2'} style={{ background: '#FFF' }}>
              <button className="btn btn-secondary mr-2" type="button" onClick={() => setModalComentario(false)}>
                Cancelar
              </button>
              <button className="btn btn-primary" type="button" onClick={() => guardarComentario()}>
                Guardar
              </button>
            </div>
          </IonModal>
        )}

        <SubHeader
          Movil={Movil}
          links={[
            { ruta: RUTAS.CATEGORIAS, titulo: 'Categorías' },
            {
              ruta: RUTAS.CATEGORIA_LISTADO_PRODUCTOS(
                ProductoSeleccionado.categoria.id,
                ProductoSeleccionado.categoria.nombre,
              ),
              titulo: ProductoSeleccionado.categoria.nombre,
            },
            { ruta: '', titulo: ProductoSeleccionado?.nombre },
          ]}
        />

        {Loading && (
          <div style={{ width: '100%', height: 500, display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
            <IonSpinner name="crescent" />
          </div>
        )}

        {!Loading && (
          <>
            {ProductoNoEncontrado && (
              <div className="page-wrapper" style={{ display: 'flex', height: '50vh' }}>
                <div className={'content-container row mx-auto mt-4'}>
                  <div className={'col-12 text-center '}>
                    <p className={'text-center text-primary'}>
                      <h3>Producto no encontrado</h3>
                    </p>
                    <p className={'text-center text-primary'}>
                      Lo sentimos el producto seleccionado no se encuentra disponible
                    </p>
                    <button className={'btn btn-primary'} onClick={() => history.push('/')}>
                      <span>Volver</span>
                    </button>
                  </div>
                </div>
              </div>
            )}
            {!ProductoNoEncontrado && !!ProductoSeleccionado && (
              <div id={'page-producto'} className="page-wrapper" style={{ display: 'flex' }}>
                <div className="container-lg background-white">
                  <div className={'content-container mx-0 px-0'}>
                    <div>
                      {!Movil && (
                        <div
                          className={'background-white'}
                          style={{
                            marginBottom: 20,
                            marginTop: 20,
                          }}
                        >
                          <div className="detalle-producto-container">
                            <ImagenesProducto />

                            <div className={'px-3'} style={{ flex: 1 }}>
                              <div className={''}>
                                <div className={'card-body background-white p-0'}>
                                  <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                                    <div
                                      style={{ overflowWrap: 'break-word', color: '#4e4e4e', fontSize: '1.25rem' }}
                                      className={'sub-titulo-producto-busqueda'}
                                    >
                                      {ProductoSeleccionado?.nombre}
                                    </div>
                                    <div style={{ display: 'flex' }}>
                                      <div
                                        style={{ cursor: 'pointer', display: 'inline-block' }}
                                        className={'no-favorito-corazon'}
                                        onClick={() => compartirProducto()}
                                        title={'Agregar a favoritos'}
                                      >
                                        <AiOutlineShareAlt size={30} />
                                      </div>
                                      <div
                                        style={{ cursor: 'pointer', display: 'inline-block' }}
                                        className={
                                          favoritoStore.esFavorito(ProductoSeleccionado as any)
                                            ? 'favorito-corazon'
                                            : 'no-favorito-corazon'
                                        }
                                        onClick={() => toggleFavorito(ProductoSeleccionado as any)}
                                        title={'Agregar a favoritos'}
                                      >
                                        <AiOutlineHeart size={30} />
                                      </div>
                                    </div>
                                  </div>
                                  {ProductoSeleccionado.disponible && ProductoSeleccionado.cantidad_disponible <= 5 && (
                                    <p className="txt-stock-bajo">
                                      <FIcon.AlertCircle size={20} /> Quedan {ProductoSeleccionado.cantidad_disponible}{' '}
                                      unidades
                                    </p>
                                  )}
                                  {ProductoSeleccionado.disponible && ProductoSeleccionado.cantidad_disponible > 5 && (
                                    <p className="txt-disponible">
                                      <FIcon.CheckCircle color="var(--success)" size={20} />
                                      Disponible
                                    </p>
                                  )}
                                  {!ProductoSeleccionado.disponible && (
                                    <p className="txt-no-disponible">
                                      <FIcon.XCircle color="var(--rojo-abi)" size={20} />
                                      Agotado temporalmente
                                    </p>
                                  )}
                                  <div style={{ display: 'flex', alignItems: 'center', gap: 5 }}>
                                    <span>
                                      <Rating
                                        name="read-only"
                                        size={'small'}
                                        value={ProductoSeleccionado.fabricante.rating}
                                        readOnly
                                      />
                                    </span>
                                    <span style={{ fontSize: 20, fontWeight: 'bolder' }}>
                                      {ProductoSeleccionado?.fabricante.rating}
                                    </span>
                                  </div>
                                  <div className={''} style={{ minWidth: '245px' }}>
                                    <div className={''}>
                                      <div className={''}>
                                        <p className={'precio-producto seleccionado'}>
                                          Gs. {numeral(ProductoSeleccionado.precio).format('0,0')}
                                        </p>
                                      </div>
                                    </div>
                                  </div>
                                </div>
                                {!ProductoSeleccionado.disponible && (
                                  <div className='d-flex' style={{gap: 10}}>
                                    <div className='btn btn-abi' onClick={notificarDisponibilidad}>
                                      {ProductoSeleccionado.notificacion && <FIcon.Check color="#FFF" size="16" className='mr-2' />}
                                      {!ProductoSeleccionado.notificacion && <FIcon.Bell color="#FFF" size="16" className='mr-2' />}
                                      Notificar disponibilidad
                                    </div>
                                    <div className='btn btn-abi' onClick={() => history.push(`${RUTAS.BUSCAR}?c=${ProductoSeleccionado.categoria.id}`)}>
                                      Productos similares
                                    </div>
                                  </div>
                                )}
                                {ProductoSeleccionado.disponible && (
                                  <div
                                    className="d-flex"
                                    style={{
                                      alignItems: 'center',
                                      justifyContent: 'space-between',
                                      flexWrap: 'wrap',
                                      gap: 30,
                                      alignSelf: 'stretch',
                                      backgroundColor: 'white',
                                    }}
                                  >
                                    <div className="d-flex flex-column" style={{ alignItems: 'center' }}>
                                      <span style={{ fontWeight: 'bolder' }}>Cantidad</span>
                                      <div className="d-flex" style={{ alignItems: 'center', gap: 30 }}>
                                        <div
                                          onClick={() => setCantidadCarrito(CantidadCarrito - 1)}
                                          style={{ cursor: 'pointer' }}
                                        >
                                          <MdRemoveCircleOutline size={!Movil ? 30 : 20} className="icono-remove" />
                                        </div>

                                        <span
                                          style={{
                                            marginLeft: 10,
                                            marginRight: 10,
                                            color: 'var(--gris-oscuro-abi)',
                                            fontSize: '1.5rem',
                                            fontWeight: 'bold',
                                          }}
                                        >
                                          <span
                                            id={'cantidad-agregar'}
                                            className={'cart-item'}
                                            data-cantidadagregar={formatCantidadCarrito(CantidadCarrito)}
                                          />
                                          {formatCantidadCarrito(CantidadCarrito)}
                                        </span>

                                        <div
                                          onClick={() => setCantidadCarrito(CantidadCarrito + 1)}
                                          style={{ cursor: 'pointer' }}
                                        >
                                          <MdAddCircleOutline size={!Movil ? 30 : 20} className="icono-add" />
                                        </div>
                                      </div>
                                    </div>

                                    <div style={{ flex: 1, display: 'flex', justifyContent: 'flex-end' }}>
                                      <button
                                        style={{
                                          whiteSpace: 'nowrap',
                                          backgroundColor: 'var(--naranja-abi)',
                                          borderColor: 'var(--naranja-abi)',
                                          paddingLeft: 50,
                                          paddingRight: 50,
                                          fontWeight: 'bolder',
                                          fontSize: '1.1rem',
                                        }}
                                        className="btn btn-primary my-2"
                                        type="button"
                                        onClick={(evt) => {
                                          if (!validarCantidadAgregarCarrito(ProductoSeleccionado, CantidadCarrito)) {
                                            return
                                          }

                                          const cagregar = document.getElementById('cantidad-agregar') as any
                                          const icarrito = document.getElementById('icon-carrito') as any
                                          cagregar.className = 'sendtocart'
                                          setTimeout(() => {
                                            cagregar.classList.remove('sendtocart')
                                            //
                                            icarrito.classList.add('shake')
                                            setTimeout(function () {
                                              icarrito.classList.remove('shake')
                                              addProductoCarrito(ProductoSeleccionado)
                                            }, 500)
                                          }, 500)
                                        }}
                                      >
                                        Agregar
                                        <span className="icono-carrito ml-2">{ICONOS.carrito}</span>
                                      </button>
                                    </div>
                                  </div>
                                )}
                              </div>
                            </div>
                          </div>
                        </div>
                      )}
                      {Movil && (
                        <div
                          className={'col-12 pt-2 px-0'}
                          style={{
                            display: 'flex',
                            flexDirection: 'column',
                            gap: 10,
                            justifyContent: 'space-between',
                          }}
                        >
                          <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                            <div>
                              <div
                                style={{ overflowWrap: 'break-word', color: '#4e4e4e', fontSize: '1.25rem' }}
                                className={'sub-titulo-producto-busqueda'}
                              >
                                {ProductoSeleccionado.nombre}
                              </div>
                              {ProductoSeleccionado.disponible && ProductoSeleccionado.cantidad_disponible <= 5 && (
                                <p className="txt-stock-bajo">
                                  <FIcon.AlertCircle size={20} /> Quedan {ProductoSeleccionado.cantidad_disponible}{' '}
                                  unidades
                                </p>
                              )}
                              {ProductoSeleccionado.disponible && ProductoSeleccionado.cantidad_disponible > 5 && (
                                <p className="txt-disponible">
                                  <FIcon.CheckCircle color="var(--success)" size={20} />
                                  Disponible
                                </p>
                              )}
                              {!ProductoSeleccionado.disponible && (
                                <p className="txt-no-disponible">
                                  <FIcon.XCircle color="var(--rojo-abi)" size={20} />
                                  Agotado temporalmente
                                </p>
                              )}
                            </div>
                            <div
                              style={{ cursor: 'pointer', display: 'inline-block' }}
                              className={'no-favorito-corazon'}
                              onClick={() => compartirProducto()}
                              title={'Agregar a favoritos'}
                            >
                              <AiOutlineShareAlt size={25} />
                            </div>
                            <div
                              style={{ cursor: 'pointer', display: 'inline-block' }}
                              className={
                                favoritoStore.esFavorito(ProductoSeleccionado as any)
                                  ? 'favorito-corazon'
                                  : 'no-favorito-corazon'
                              }
                              onClick={() => toggleFavorito(ProductoSeleccionado as any)}
                              title={'Agregar a favoritos'}
                            >
                              <AiOutlineHeart size={25} />
                            </div>
                          </div>
                          <ImagenesProducto />
                        </div>
                      )}
                      <div className="drop-producto-detail" style={{ marginTop: 70 }}>
                        <div
                          style={{ display: 'flex', justifyContent: 'space-between' }}
                          onClick={() => setShowDescription(!ShowDescription)}
                        >
                          <h3 className="titulo-producto">Descripción</h3>
                          {ShowDescription ? <FIcon.ChevronDown /> : <FIcon.ChevronLeft />}
                        </div>
                        {ShowDescription && (
                          <p className={'titulo-producto mt-2'}>{ProductoSeleccionado.descripcion}</p>
                        )}
                      </div>
                      <div className="drop-producto-detail" style={{ marginTop: 20 }}>
                        <div
                          style={{ display: 'flex', justifyContent: 'space-between' }}
                          onClick={() => setShowEspecificaciones(!ShowEspecificaciones)}
                        >
                          <h3 className="titulo-producto">Especificaciones </h3>
                          {ShowEspecificaciones ? <FIcon.ChevronDown /> : <FIcon.ChevronLeft />}
                        </div>
                        {ShowEspecificaciones && (
                          <p className={'titulo-producto mt-2'}>{ProductoSeleccionado.descripcion_extensa}</p>
                        )}
                        {ShowEspecificaciones && !ProductoSeleccionado.descripcion_extensa && (
                          <p className={'titulo-producto mt-2'}>Este producto no posee especificaciones</p>
                        )}
                      </div>
                      <div className="drop-producto-detail" style={{ marginTop: 20 }}>
                        <div
                          style={{ display: 'flex', justifyContent: 'space-between' }}
                          onClick={() => setShowComentarios(!ShowComentarios)}
                        >
                          <h3 className="titulo-producto">Comentarios</h3>
                          {ShowComentarios ? <FIcon.ChevronDown /> : <FIcon.ChevronLeft />}
                        </div>
                        {ShowComentarios && (
                          <>
                            {ProductoSeleccionado.comentarios.map((comentario: any) => (
                              <div className={'comentario-producto'}>
                                <div>
                                  <img
                                    alt="Imagen de perfil"
                                    className={'imagen-perfil'}
                                    src={comentario.usuario.imagen_perfil || 'assets/icon/person-line.png'}
                                  />
                                  {comentario.usuario.first_name} {comentario.usuario.last_name}
                                </div>
                                <div className={'mt-2'}>
                                  Comentado el {moment(comentario.fecha).format('DD \\d\\e MMMM \\d\\e\\l YYYY')}
                                </div>
                                <div className={'mt-2'}>{comentario.comentario}</div>
                              </div>
                            ))}
                            <button
                              style={{
                                whiteSpace: 'nowrap',
                                backgroundColor: 'var(--naranja-abi)',
                                borderColor: 'var(--naranja-abi)',
                                paddingLeft: 20,
                                paddingRight: 20,
                                fontWeight: 'bolder',
                                fontSize: 15,
                              }}
                              className="btn btn-primary"
                              type="button"
                              onClick={() => {
                                if (!loginStore.isLoggedIn) {
                                  addToast('Inicie sesión para agregar comentarios')
                                  return
                                }
                                setModalComentario(true)
                              }}
                            >
                              Agregar comentario
                            </button>
                          </>
                        )}
                      </div>
                      {ProductoSeleccionado.video && (
                        <div className="drop-producto-detail">
                          <div
                            style={{ display: 'flex', justifyContent: 'space-between' }}
                            onClick={() => setShowDescription(!ShowDescription)}
                          >
                            <h3 className="titulo-producto">Video </h3>
                          </div>
                          <video
                            src={ProductoSeleccionado.video}
                            controls
                            style={{ width: 640, height: 360, maxWidth: '100%' }}
                          />
                        </div>
                      )}
                      {!Loading && ProductoSeleccionado.productos_relacionados && (
                        <div style={{ marginTop: 10 }}>
                          {ProductoSeleccionado.productos_relacionados.length > 0 && (
                            <CarouselProductos
                              Movil={Movil}
                              productos={ProductoSeleccionado.productos_relacionados}
                              titulo={'Productos similares'}
                            />
                          )}
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            )}
            {Movil && (
              <div className="flotante-agregar-carrito">
                {ProductoSeleccionado && ProductoSeleccionado.disponible && (
                  <div
                    className="d-flex"
                    style={{
                      paddingBottom: 40,
                      alignItems: 'flex-start',
                      justifyContent: 'space-evenly',
                      flex: 1,
                    }}
                  >
                    <div className="d-flex flex-column" style={{ alignItems: 'center' }}>
                      <span style={{ fontWeight: 'bolder' }}>Cantidad</span>
                      <div className="d-flex" style={{ alignItems: 'center', gap: 30 }}>
                        <div onClick={() => setCantidadCarrito(CantidadCarrito - 1)}>
                          <MdRemoveCircleOutline size={!Movil ? 30 : 20} className="icono-remove" />
                        </div>

                        <span
                          style={{
                            marginLeft: 5,
                            marginRight: 5,
                            color: 'var(--gris-oscuro-abi)',
                          }}
                        >
                          <span
                            id={'cantidad-agregar'}
                            className={'cart-item'}
                            data-cantidadagregar={formatCantidadCarrito(CantidadCarrito)}
                          />
                          {formatCantidadCarrito(CantidadCarrito)}
                        </span>

                        <div onClick={() => setCantidadCarrito(CantidadCarrito + 1)}>
                          <MdAddCircleOutline size={!Movil ? 30 : 20} className="icono-add" />
                        </div>
                      </div>
                    </div>

                    <button
                      style={{
                        whiteSpace: 'nowrap',
                        backgroundColor: 'var(--naranja-abi)',
                        borderColor: 'var(--naranja-abi)',
                        paddingLeft: 20,
                        paddingRight: 20,
                        fontWeight: 'bolder',
                        fontSize: 15,
                      }}
                      className="btn btn-primary"
                      type="button"
                      onClick={() => {
                        if (!validarCantidadAgregarCarrito(ProductoSeleccionado, CantidadCarrito)) {
                          return
                        }

                        const cagregar = document.getElementById('cantidad-agregar') as any
                        const icarrito = document.getElementById('icon-carrito') as any
                        cagregar.className = 'sendtocart'
                        setTimeout(() => {
                          cagregar.classList.remove('sendtocart')
                          //
                          icarrito.classList.add('shake')
                          setTimeout(function () {
                            icarrito.classList.remove('shake')
                            addProductoCarrito(ProductoSeleccionado)
                          }, 500)
                        }, 500)
                      }}
                    >
                      Agregar
                      <span className="icono-carrito">{ICONOS.carrito}</span>
                    </button>
                  </div>
                )}
                {!ProductoSeleccionado.disponible && (
                  <div className='d-flex' style={{gap: 10}}>
                    <div className='btn btn-abi' onClick={notificarDisponibilidad}>
                      {ProductoSeleccionado.notificacion && <FIcon.Check color="#FFF" size="16" className='mr-2' />}
                      {!ProductoSeleccionado.notificacion && <FIcon.Bell color="#FFF" size="16" className='mr-2' />}
                      Notificar disponibilidad
                    </div>
                    <div className='btn btn-abi' onClick={() => history.push(`${RUTAS.BUSCAR}?c=${ProductoSeleccionado.categoria.id}`)}>
                      Productos similares
                    </div>
                  </div>
                )}
              </div>
            )}
          </>
        )}
      </Page>

      {!!ProductoSeleccionado && ProductoSeleccionado.cantidad_disponible > 0 && (
        <button
          style={{
            whiteSpace: 'nowrap',
            backgroundColor: '#fc4c02',
            borderColor: '#fc4c02',
            fontWeight: 'bold',
          }}
          className="btn btn-primary my-2"
          type="button"
          onClick={() => {
            if (!validarCantidadAgregarCarrito(ProductoSeleccionado, CantidadCarrito)) {
              return
            }

            addProductoCarrito(ProductoSeleccionado)
          }}
        >
          <FIcon.ShoppingCart size={20} style={{ color: 'white', marginBottom: 2 }} /> Agregar al carrito
        </button>
      )}
    </div>
  )
})

export default ProductoDetail
