import './HomeSidebar.css';

import React, {useEffect, useState} from 'react';
import * as FIcon from "react-feather";
import {RUTAS, RUTAS_APP_FABRICANTE} from "../../constants";
import {IonAlert, IonModal} from "@ionic/react";
import {Capacitor} from "@capacitor/core";
import {useHistory} from "react-router-dom";
import {useToaster} from "../../helpers-ts";
import { App } from '@capacitor/app'
import {useStores} from "../../models/root-store/root-store-context";
import { RELEASE } from '../..'
import { ICONOS } from '../../helpers-ts/iconos'

interface HomeTransportistaSidebarProps {
  visible: boolean
  set_visible: any
  fabricante?: any
  transportista?: any
  usuario?: any
}

export const HomeSidebar: React.FC<HomeTransportistaSidebarProps> = React.forwardRef((props, ref) => {
  const rootStore = useStores()
  const [Alert, setAlert] = useState(false)
  const addToast = useToaster()
  const history = useHistory()


  useEffect(() => {
    if (props.visible === true){
      props.set_visible(true)
    } else {
      props.set_visible(false)
    }
  }, [props.visible])

  useEffect(() => {
    props.set_visible(false)
  },[history])

  const open_page = (page: string) => {
    props.set_visible(false)
    history.push(page)
  }

  const sidebar_content =
    <div className={'d-flex flex-column h-100 overflow-auto'}>
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={'Cerrar sesión'}
        message={'Confirma que desea cerrar sesión?'}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Confirmar',
            handler: () => open_page(RUTAS.LOGOUT)
          }
        ]}
      />
      <div className={'fabricante-home-sidebar-header'}>
        <div className={'fabricante-home-sidebar-header-badge'} onClick={() => open_page(RUTAS_APP_FABRICANTE.PERFIL)}>
          <img alt="Imagen de perfil" className={'imagen-perfil'} src={props.fabricante && props.fabricante.imagen_perfil ? props.fabricante.imagen_perfil : 'assets/person.png'}/>
        </div>
        <div className={'fabricante-home-sidebar-header-name'} onClick={
          () => open_page(RUTAS_APP_FABRICANTE.PERFIL)}>
          <span className={'text-truncate'}>¡Hola, {props.fabricante.nombre}!</span>
          <small className={'text-white'}>id#{props.fabricante && props.fabricante.id}</small>
        </div>
      </div>
      <div className={'fabricante-home-sidebar-menu'}>

        <a onClick={() => open_page(RUTAS_APP_FABRICANTE.HOME)}>
          {ICONOS.abi}
          Inicio
        </a>

        <a onClick={() => open_page(RUTAS_APP_FABRICANTE.CATALOGO)}>
          {ICONOS.caja}
          Catálogo
        </a>

        <a onClick={() => open_page(RUTAS_APP_FABRICANTE.BALANCE)}>
          {ICONOS.facturacion}
          Balance
        </a>

        <a onClick={() => open_page(RUTAS_APP_FABRICANTE.PEDIDOS)}>
          <img src="assets/icon/todo.png" />
          Pedidos
        </a>

        <a onClick={() => open_page(RUTAS_APP_FABRICANTE.VENTAS)}>
          {ICONOS.movimientos}
          Ventas
        </a>

        <a onClick={() => open_page(RUTAS_APP_FABRICANTE.ATENCION_CLIENTE)}>
          {ICONOS.atencion_cliente}
          Contactanos
        </a>

        <a onClick={() => open_page(RUTAS_APP_FABRICANTE.CAMBIO_MODO)}>Modo de usuario</a>

        <a onClick={() => open_page(RUTAS_APP_FABRICANTE.ATENCION_CLIENTE)}>
          Ayuda <FIcon.ChevronRight size={'1rem'}/>
        </a>

        <a href={'https://app.abi.com.py/terminos-fabricante/'} target={"_blank"}>
          {ICONOS.documento}
          Términos y condiciones
        </a>

        <a onClick={() => open_page(RUTAS_APP_FABRICANTE.ATENCION_CLIENTE)}>
          {ICONOS.documento}
          Acuerdo comercial
        </a>

        {Capacitor.getPlatform() !== 'web' && <a onClick={() => App.exitApp()}>Salir</a>}

        <a className={'cerrar-sesion'} onClick={() => setAlert(true)}>Cerrar sesión</a>

        <div id={'release-container'}>v{RELEASE.split('@')[1]}</div>
      </div>
  </div>

  return (
    <IonModal
      animated={false}
      isOpen={props.visible}
      cssClass='fabricante-home-sidebar'
      swipeToClose={true}
      backdropDismiss={true}
      onDidDismiss={() => props.set_visible(false)}
    >
      {sidebar_content}
    </IonModal>
  )
});

export default HomeSidebar;
