import { IonContent, IonPage } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { MODO_USO_APP } from '../constants'
import { useStores } from '../models/root-store/root-store-context'
import Header from './Fabricante/Header'
import { TituloCustom } from './TituloCustom'
import BarraEstadoTransportista from './Transportista/BarraEstadoTransportista'

type PageAppFabricanteProps = {
  children?: any
  header?: any
  footer?: any
  pageStyles?: any
  customStyles?: any
  accion_boton_izquierdo?: any
  accion_boton_derecho?: any
  boton_izquierdo?: any
  boton_derecho?: any
  titulo?: string
  titulo_header?: string | null
  sub_titulo?: string | null
  mostrar_barra?: boolean
  subBarraInternaHeader?: any
  subBarraExternaHeader?: any
  id?: string
  no_scroll?: boolean
  preset?: 'p-driver' | null
}

export const PageApp: React.FC<PageAppFabricanteProps> = observer((props) => {
  const rootStore = useStores()
  const { loginStore, transportistaStore } = rootStore
  const footer = props.footer || null
  const tranportistaStyles =
    props.preset === 'p-driver'
      ? 'background-blanco perfil-transportista ' +
        (transportistaStore.transportista?.aprobado ? 'tranportista-double-header-wrapper' : '')
      : ''
  const pageStyles = props.pageStyles || '' + tranportistaStyles
  const customStyles = props.customStyles || {}
  const accionBotonIzquierdo = props.accion_boton_izquierdo ? props.accion_boton_izquierdo : null
  const [modalRegistro, setModalRegistro] = useState<'login' | 'registro' | null>(null)
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)
  const subBarraInterna = props.subBarraInternaHeader ? (
    props.subBarraInternaHeader
  ) : props.preset === 'p-driver' ? (
    <BarraEstadoTransportista  goBack/>
  ) : null

  useEffect(() => {
    rootStore.setEsMovil(Movil)
  }, [])

  useEffect(() => {
    if (!loginStore.isLoggedIn) {
      setModalRegistro('login')
    }
  }, [])

  let color
  if (loginStore.modo_uso_app === MODO_USO_APP.transportista) {
    color = '#ff6f33'
  } else if (loginStore.modo_uso_app === MODO_USO_APP.proveedor) {
    color = '#303AB2'
  } else if (loginStore.modo_uso_app === MODO_USO_APP.cliente) {
    color = '#ffb434'
  }

  return (
    <IonPage className={'abi-page-wrapper ' + pageStyles} style={customStyles}>
      <TituloCustom titulo={props.titulo} />
      <Header
        accionBotonIzquierdo={accionBotonIzquierdo}
        botonIzquierdo={props.boton_izquierdo}
        accionBotonDerecho={() => props.accion_boton_derecho && props.accion_boton_derecho()}
        botonDerecho={props.boton_derecho}
        titulo={props.titulo_header}
        sub_titulo={props.sub_titulo}
        mostrarBarra={props.mostrar_barra}
        subBarraInterna={subBarraInterna}
        subBarraExterna={props.subBarraExternaHeader}
        color={color}
      />

      <div className={'abi-page-content'} id={props.id ? props.id : ''}>
        {!props.no_scroll && <IonContent scrollEvents={true}>{props.children}</IonContent>}
        {props.no_scroll && <div>{props.children}</div>}
      </div>

      {footer}
    </IonPage>
  )
})

export default PageApp
