import React from 'react';
import {observer} from "mobx-react-lite";
import {Helmet} from 'react-helmet';
import {useRouteMatch} from "react-router-dom";
import {getTituloPag} from "../helpers";

type TituloCustomProps = {
  titulo?: string
};

export const TituloCustom: React.FC<TituloCustomProps> = observer((props) => {
  return <Helmet>
      <title>{props.titulo ? ("ABi | " + props.titulo) : getTituloPag(useRouteMatch)}</title>
  </Helmet>
});

export default TituloCustom;
