import { IonToggle } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router'

import { RUTAS_APP } from '../../constants'
import { useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { ItinerarioApi } from '../../services/api'
import * as FIcon from 'react-feather'

interface Props {
  goBack?: boolean
}

export const BarraEstadoTransportista: React.FC<Props> = observer((props) => {
  const rootStore = useStores()
  const { transportistaStore } = rootStore
  const [Itinerario, setItinerario] = useState<ItinerarioApi | null>()
  const [Activo, setActivo] = useState(false)
  const history = useHistory()
  const addToast = useToaster()

  useEffect(() => {
    setItinerario(transportistaStore.itinerarioActivo)
  }, [transportistaStore.itinerarioActivo])

  useEffect(() => {
    if (transportistaStore.transportista) {
      setActivo(transportistaStore.transportista?.activo === true)
    }
  }, [])

  const handleActivoChange = () => {
    if (!!Itinerario) {
      addToast('Finalice su viaje para cambiar de estado', 'warning')
      return
    }
    const checked = !Activo
    rootStore.environment.api.setConexionTransportista(checked).then((result) => {
      if (result.kind === 'ok') {
        setActivo(checked)
        transportistaStore.transportista?.setActivo(checked)
        addToast('Estado actualizado exitosamente', 'success')
      } else {
        addToast('Error al actualizar el estado, vuelva a intentar en unos momentos.', 'error')
      }
    })
  }

  return (
    <>
      {transportistaStore.transportista?.aprobado && (
        <div className="d-flex align-items-center col-12 mt-2">
          {props.goBack && (
            <div onClick={() => history.goBack()}>
              <FIcon.ChevronLeft color="white" />
            </div>
          )}
          <span onClick={handleActivoChange}>
            <IonToggle className="toggle-green" checked={Activo} style={{ pointerEvents: 'none' }} />
          </span>
          <span style={{ color: 'white', fontWeight: 'bolder' }}>
            {!!Itinerario && <>Driver al volante</>}
            {!Itinerario && !Activo && <>Fuera de servicio</>}
            {!Itinerario && Activo && <>Conectado</>}
          </span>
          <div className="d-flex align-items-center justify-content-end flex-grow-1">
            <a onClick={() => history.push(RUTAS_APP.GANANCIA)}>
              <img src={'assets/icon/facturacion.png'} className="filter-invert-imagen icono-header" />
            </a>
          </div>
        </div>
      )}
    </>
  )
})

export default BarraEstadoTransportista
