import { Capacitor } from '@capacitor/core'
import { OneSignal as OneSignalCapacitor } from '@ionic-native/onesignal'
import { IonAlert, IonModal } from '@ionic/react'
import { SnapshotOut } from 'mobx-state-tree'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { useHistory } from 'react-router-dom'
import { RUTAS, RUTAS_APP, RUTAS_APP_FABRICANTE } from '../../constants'
import { useApiToaster, useClearStore, useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Transportista as TransportistaType } from '../../models/types/Transportista'
import './HomeSidebarTransportista.css'

interface HomeTransportistaSidebarProps {
  visible: boolean
  set_visible: any
}

export const HomeSidebarTransportista: React.FC<HomeTransportistaSidebarProps> = React.forwardRef((props, ref) => {
  const rootStore = useStores()
  const { loginStore, transportistaStore } = rootStore
  const [DropAyuda, setDropAyuda] = useState(false)
  const [Transportista, setTransportista] = useState<SnapshotOut<typeof TransportistaType>>()
  const addToast = useToaster()
  const history = useHistory()
  const apiToaster = useApiToaster()
  const [Alert, setAlert] = useState(false)
  const clearStore = useClearStore()

  const cerrarSesion = () => {
    props.set_visible(false)
    if (Capacitor.getPlatform() !== 'web') {
      history.push(RUTAS.LOGOUT)
    } else {
      addToast('Sesión finalizada', 'info')
      clearStore()
      document.location.href = '/'
    }
  }

  useEffect(() => {
    if (!props.visible) {
      setDropAyuda(false)
    }
    if (props.visible === true) {
      props.set_visible(true)
    } else {
      props.set_visible(false)
    }
  }, [props.visible])

  useEffect(() => {
    if (!!transportistaStore.transportista) {
      setTransportista(transportistaStore.transportista)
    }
  }, [transportistaStore.transportista])

  useEffect(() => {
    props.set_visible(false)
  }, [history])

  const open_page = (page: string) => {
    props.set_visible(false)
    history.push(page)
  }

  const activar_desactivar = (activo: boolean, tipo: number) => {
    if (tipo === 10) {
      const success_msg = activo ? 'Notificaciones activadas' : 'Notificaciones desactivadas'
      const error_msg = activo ? 'activar las notificaciones' : 'desactivar las notificaciones'
      if (Capacitor.getPlatform() !== 'web') {
        OneSignalCapacitor.getIds().then((ids) => {
          loginStore.setOneSignal(ids.userId, ids.pushToken)
          loginStore.environment.api.setDevice(ids.userId, ids.pushToken, activo).then((result) => {
            apiToaster(result, success_msg, error_msg)
            if (result.kind === 'ok') {
              loginStore.setNotificacionesActivas(activo)
            }
          })
        })
      }
    } else {
      loginStore.setSonidosActivos(activo)
      const success_msg = activo ? 'Sonidos activados' : 'Sonidos desactivados'
      addToast(success_msg, 'success')
    }
  }

  const sidebar_content = (
    <>
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={'Cerrar sesión'}
        message={'Confirma que desea cerrar sesión?'}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Confirmar',
            handler: () => cerrarSesion(),
          },
        ]}
      />
      {!!Transportista && !!Transportista.usuario && (
        <div className={'d-flex flex-column h-100 overflow-auto'}>
          <div className={'transportista-home-sidebar-header'}>
            <>
              <div className={'transportista-home-sidebar-header-badge'} onClick={() => open_page(RUTAS_APP.PERFIL)}>
                <div className={'main-container'}>
                  <img
                    alt="Imagen de perfil"
                    className={'imagen-perfil'}
                    src={
                      Transportista && Transportista.usuario.imagen_perfil
                        ? Transportista.usuario.imagen_perfil
                        : 'assets/person.png'
                    }
                  />
                </div>
              </div>

              <div className={'transportista-home-sidebar-header-name'} onClick={() => open_page(RUTAS_APP.PERFIL)}>
                <div>
                  <span className="transportista-home-sidebar-name">¡Hola,</span>
                  <br />
                  <span className="transportista-home-sidebar-name">
                    {Transportista.usuario.first_name} {Transportista.usuario.last_name}!
                  </span>
                  <br />
                  <small className={'text-white'}>id#{Transportista && Transportista.id}</small>
                </div>
              </div>
            </>
          </div>

          <div className={'transportista-home-sidebar-menu'}>
            <div>
              <a onClick={() => open_page(RUTAS_APP.HOME)}>Inicio</a>
            </div>
            <div>
              <img alt={''} src={'assets/icon/oportunidades.png'} />
              <a
                onClick={() => {
                  const aprobados = transportistaStore.transportista?.vehiculos.filter((veh) => veh.aprobado) as any
                  if (!transportistaStore.transportista?.aprobado) {
                    addToast('Debe tener su perfil aprobado para acceder a las oportunidades.')
                  } else if (aprobados.length < 1) {
                    addToast('Debe poseer por lo menos un vehículo aprobado para acceder a las oportunidades.')
                  } else {
                    open_page(RUTAS_APP.OPORTUNIDADES)
                  }
                }}
              >
                Oportunidades
              </a>
            </div>
            <div>
              <img alt={''} src={'assets/icon/facturacion.png'} />
              <a onClick={() => open_page(RUTAS_APP.GANANCIA)}>Balance</a>
            </div>
            <div>
              <img src="assets/icon/historial_entegas.png" />
              <a onClick={() => open_page(RUTAS_APP.MIS_RUTAS)}>Historial de entregas</a>
            </div>
            <div>
              <img alt={''} src={'assets/icon/atencion-cliente.png'} />
              <a href="https://wa.me/595981840840">Contactanos</a>
            </div>
            <div>
              <a onClick={() => setDropAyuda(!DropAyuda)}>Ayuda</a>
              {!DropAyuda && <FIcon.ChevronRight />}
              {DropAyuda && <FIcon.ChevronDown />}
            </div>
            {DropAyuda && (
              <>
                <div>
                  <img alt={''} src={'assets/icon/info.png'} />
                  <a onClick={() => open_page(RUTAS_APP_FABRICANTE.PREGUNTAS_FRECUENTES)}>Preguntas Frecuentes</a>
                </div>
                <div>
                  <img alt={''} src={'assets/icon/comentarios.png'} />
                  <a onClick={() => open_page(RUTAS_APP_FABRICANTE.COMENTARIOS_SUGERENCIAS)}>
                    Comentarios y Sugerencias
                  </a>
                </div>
                <div>
                  <img alt={''} src={'assets/icon/bug.png'} />
                  <a onClick={() => open_page(RUTAS_APP_FABRICANTE.REPORTAR_ERROR)}>Reportar un Error</a>
                </div>
              </>
            )}
            <div>
              <img alt={''} src={'assets/icon/documento.png'} />
              <a href={'https://app.abi.com.py/terminos-fabricante/'} target={'_blank'}>
                Términos y condiciones
              </a>
            </div>
            <div
              className={'col-12 mt-3 px-0'}
              style={{ marginLeft: 10, fontWeight: 'bolder' }}
              onClick={() => setAlert(true)}
            >
              <span>Cerrar sesión</span>
            </div>
          </div>
        </div>
      )}
    </>
  )

  return (
    <IonModal
      animated={false}
      isOpen={props.visible}
      cssClass="transportista-home-sidebar"
      swipeToClose={true}
      backdropDismiss={true}
      onDidDismiss={() => props.set_visible(false)}
    >
      {sidebar_content}
    </IonModal>
  )
})

export default HomeSidebarTransportista
