import './ProductoCard.css'

import { Rating } from '@material-ui/lab'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import { IonAlert } from '@ionic/react'
import { MdAddCircleOutline, MdRemoveCircleOutline } from 'react-icons/md'
import { useHistory } from 'react-router-dom'
import { RUTAS } from '../constants'
import { formatCantidadCarrito, plata_fmt } from '../helpers'
import { useStores } from '../models/root-store/root-store-context'
import { formatTextoURL, useToaster, useVerificarLoginEdad } from '../helpers-ts'
import { iProducto } from '../models/favorito-store/favorito-store'
import { ICONOS } from '../helpers-ts/iconos'

export interface iCardState {}

type ProductosListadoProps = {
  producto: iProducto
  index?: number
  titulo?: string
  containerClass?: string
}

export const ProductoCard: React.FC<ProductosListadoProps> = observer((props) => {
  const history = useHistory()
  const rootStore = useStores()
  const { loginStore, carritoStore, favoritoStore } = rootStore

  const [CantidadCarrito, setCantidadCarrito] = useState(1)
  const [AlertBloqueoTitle, setAlertBloqueoTitle] = useState<string>('')
  const [AlertBloqueoMessage, setAlertBloqueoMessage] = useState<string>('')
  const [AlertBloqueoAction, setAlertBloqueoAction] = useState<any>(null)
  const [AlertBloqueo, setAlertBloqueo] = useState<boolean>(false)
  const containerClass = props.containerClass !== undefined ? props.containerClass : 'col-6 col-lg-4 mb-4'

  const Movil = /Mobi/.test(navigator.userAgent)
  const verificarEdadEstado = useVerificarLoginEdad()
  const addToast = useToaster()

  const producto = props.producto
  const imagenProducto = producto.imagenes
    ? producto.imagenes?.length > 0
      ? producto.imagenes[0].imagen
      : 'assets/abi-logo-producto.png'
    : 'assets/abi-logo-producto.png'

  const producto_detalle = (producto: any) => {
    history.push(RUTAS.PRODUCTO_DETAIL(producto.id, formatTextoURL(producto.nombre)))
  }

  const toggleFavorito = (producto: iProducto) => {
    if (!loginStore.isLoggedIn) {
      addToast('Inicie sesión para agregar a sus favoritos.')
      return
    }
    const fav = favoritoStore.favorito(producto)
    if (!!fav) {
      const estado = fav.toggleActivo()
      if (fav.id) {
        rootStore.environment.api.updateFavorito(fav.id, estado).then((result) => {
          if (result.kind === 'ok') {
            console.log(result)
          }
        })
      }
    } else {
      favoritoStore.addFavorito(producto)
      rootStore.environment.api.addFavorito(producto.id).then((result) => {
        if (result.kind === 'ok') {
          rootStore.environment.api.getFavoritos().then((result) => {
            if (result.kind === 'ok') {
              favoritoStore.setFavoritosAPI(result.data)
            }
          })
        }
      })
    }
  }

  const validarCantidadAgregarCarrito = (producto: iProducto, cantidad: any) => {
    let obj = carritoStore.productos.find((o) => o.id === producto.id)
    if (obj) {
      if (obj.cantidad + cantidad > producto.cantidad_disponible) {
        addToast(`No hay suficiente cantidad en stock. Cantidad disponible: ${producto.cantidad_disponible}.`)
        return false
      }
    } else {
      if (cantidad > producto.cantidad_disponible) {
        addToast(`No hay suficiente cantidad en stock. Cantidad disponible: ${producto.cantidad_disponible}.`)
        return false
      }
    }
    return true
  }

  const addProductoCarrito = (producto: iProducto, cantidad: any) => {
    if (producto.no_apto_menores) {
      const estado = verificarEdadEstado()
      if (estado.case !== 10) {
        setAlertBloqueoTitle(estado.title)
        setAlertBloqueoMessage(estado.message)
        setAlertBloqueoAction(estado.action)
        setAlertBloqueo(true)
        return false
      }
    }
    let obj = carritoStore.productos.find((o) => o.id === producto.id)
    let productoCarrito
    if (obj) {
      for (let i = 0; i < cantidad; i++) {
        carritoStore.sumarProducto(producto)
      }
    } else {
      productoCarrito = {
        ...producto,
        cantidad: cantidad,
        fabricante: producto.fabricante.nombre,
        imagenes: producto.imagenes ? (producto.imagenes.length > 0 ? producto.imagenes[0].imagen : '') : '',
        categoria: producto.categoria?.nombre,
      }
      carritoStore.addProducto(productoCarrito)
    }
    if (loginStore.isLoggedIn) {
      rootStore.environment.api.agregarEliminarProductoCarrito(producto.id, null, cantidad).then((result) => {
        if (result.kind === 'ok') {
        }
      })
    }
    setCantidadCarrito(1)
  }

  return (
    <>
      <IonAlert
        isOpen={AlertBloqueo}
        onDidDismiss={() => setAlertBloqueo(false)}
        header={'Acceso restringido'}
        message={AlertBloqueoMessage}
        buttons={
          AlertBloqueoAction
            ? [
                {
                  text: 'Cancelar',
                  role: 'cancel',
                  cssClass: 'secondary',
                },
                {
                  text: AlertBloqueoTitle,
                  handler: AlertBloqueoAction,
                },
              ]
            : [
                {
                  text: 'Cancelar',
                  role: 'cancel',
                  cssClass: 'secondary',
                },
              ]
        }
      />
      <div className={containerClass}>
        <div className={'container-tarjeta-producto'}>
          <div className="container-imagen-card-producto" onClick={() => producto_detalle(producto)}>
            <img src={imagenProducto} alt={'Imagen de producto'} style={{ width: 400, height: 400 }} />
          </div>
          <div
            style={{
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'center',
              alignItems: 'flex-start',
              paddingLeft: 10,
              paddingRight: 10,
            }}
          >
            <div>
              <Rating name="read-only" size={'small'} value={producto.fabricante.rating} readOnly />
            </div>

            <a>
              <span
                className={'sub-titulo-producto-inicio'}
                style={{
                  whiteSpace: 'pre-wrap',
                  display: '-webkit-box',
                  WebkitBoxOrient: 'vertical',
                  WebkitLineClamp: 2,
                  textAlign: 'start',
                  overflow: 'hidden',
                  textOverflow: 'ellipsis',
                  fontSize: Movil ? 12 : 16,
                  maxHeight: 60,
                }}
                onClick={() => producto_detalle(producto)}
              >
                {producto.nombre}
              </span>
            </a>

            <p
              onClick={() => producto_detalle(producto)}
              className={'sub-titulo-producto-inicio-precio'}
              style={{ fontSize: Movil ? 14 : 20, fontWeight: 'bolder' }}
            >
              {plata_fmt(producto.precio)}
            </p>
          </div>
          <div className={'agregar-container'} style={{ display: 'flex', alignItems: 'flex-end', width: 'inherid' }}>
            <div
              style={{ flex: 1, display: 'flex', justifyContent: 'space-between', paddingLeft: 10, paddingRight: 10 }}
            >
              <div style={{ flex: 1, display: 'flex', justifyContent: 'flex-start' }}>
                <div onClick={() => toggleFavorito(producto)}>
                  <span className={`icn-favoritos ${favoritoStore.esFavorito(producto) ? 'favorito-corazon' : 'no-favorito-corazon'}`}>
                    {ICONOS.favorito}
                  </span>
                </div>
              </div>
              <div style={{ display: 'flex', justifyContent: 'center' }}>
                <div
                  onClick={() => {
                    if (CantidadCarrito > 0) {
                      setCantidadCarrito(CantidadCarrito - 1)
                    }
                  }}
                >
                  <MdRemoveCircleOutline className="icono-remove icn-add-remove" />
                </div>

                <span
                  style={{
                    marginLeft: 10,
                    marginRight: 10,
                    color: 'var(--gris-claro-abi)',
                    fontWeight: 'bold',
                    fontSize: '1.25rem',
                  }}
                >
                  <span
                    className={'cart-item cantidad-agregar cantidad-agregar-' + producto.id}
                    data-cantidadagregar={formatCantidadCarrito(CantidadCarrito)}
                  />
                  {formatCantidadCarrito(CantidadCarrito)}
                </span>

                <div onClick={() => setCantidadCarrito(CantidadCarrito + 1)}>
                  <MdAddCircleOutline className="icono-add icn-add-remove" />
                </div>
              </div>
              <div
                onClick={(evt: any) => {
                  if (!validarCantidadAgregarCarrito(producto, CantidadCarrito)) {
                    return
                  }

                  const cagregar = evt.target
                    .closest('.agregar-container')
                    .getElementsByClassName('cantidad-agregar')[0]
                  const icarrito = document.getElementById('icon-carrito') as any
                  cagregar.classList.add('sendtocart')
                  setTimeout(() => {
                    cagregar.classList.remove('sendtocart')
                    //
                    icarrito.classList.add('shake')
                    setTimeout(function () {
                      icarrito.classList.remove('shake')
                      addProductoCarrito(producto, CantidadCarrito)
                    }, 500)
                  }, 500)
                }}
                style={{ flex: 1, display: 'flex', justifyContent: 'flex-end' }}
                className={'carrito-card'}
              >
                <span className='icn-carrito'>
                  {ICONOS.carrito}
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
})

export default ProductoCard
